import axios from "axios";
import { ToastWrapper } from "../utils";

export const axiosWrapper = axios.create({
  baseURL: process.env.REACT_APP_BASE_URL || "http://localhost:5000",
  // timeout: 10000,
  headers: {},
});

const handleResponse = (res) => {
  return res.data;
};

const handleError = (err) => {
  console.log(err)
  let errorMsg = "";
  switch (err.response?.status) {
    case 400:
      errorMsg = "Lỗi nhập liệu";
      break;
    case 401:
      errorMsg = "Người dùng chưa đăng nhập";
      break;
    case 403:
      errorMsg = "Tài nguyên không được phép";
      break;
    case 404:
      errorMsg = "Không tìm thấy tài nguyên";
      break;
    case 500:
      errorMsg = "Không thể kết nối đến server";
      break;
    default:
      errorMsg = "Lỗi chưa xác định";
      break;
  }

  return ToastWrapper(errorMsg);
};

axiosWrapper.interceptors.request.use(
  (req) => {
    return req;
  },
  (err) => {
    console.log(err);
  }
);

axiosWrapper.interceptors.response.use(handleResponse, handleError);
